import * as Actions from '../actions';

const initialState = {
  totalPrice:0
};

const cart = (state = initialState, action) => {
    switch (action.type) {
      case Actions.TOTAL_PRICE: {
        return {totalPrice:action.payload
        };
      }
      default: {
        return state;
      }
    }
  };
  
  export default cart;